<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'

  export default {
    props: {
      probeType: {
        type: Number,
        default: 1 // 1 3
      },
      click: {
        type: Boolean,
        default: true
      },
      listenScroll: { //
        type: Boolean,
        default: false
      },
      data: { // 
        type: Array,
        default: null
      },
      pullup: {  // 
        type: Boolean,
        default: false
      },
      beforeScroll: {  // 
        type: Boolean,
        default: false
      },
      refreshDelay: { //
        type: Number,
        default: 20
      }
    },
    mounted() {
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    methods: {
      _initScroll() {   // scroll
        if (!this.$refs.wrapper) {
          return
        }
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click
        })

        if (this.listenScroll) {   // listview.vue
          let me = this
          this.scroll.on('scroll', (pos) => {
            me.$emit('scroll', pos)
          })
        }

        if (this.pullup) {  // suggest
          this.scroll.on('scrollEnd', () => { // scrollEnd()            
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollToEnd') // scrollToEndscrollToEnd
            }
          })
        }

        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll') // beforeScrollsuggest
          })
        }
      },
      disable() { //  better-scroll
        this.scroll && this.scroll.disable()
      },
      enable() { //  better-scroll,  better-scroll
        this.scroll && this.scroll.enable()
      },
      refresh() { //  better-scroll DOM 
        this.scroll && this.scroll.refresh()
      },
      scrollTo() { // 
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {  // 
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },
    watch: {
      data() {  //  refresh
        setTimeout(() => {
          this.refresh()
        }, this.refreshDelay)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">

</style>
