// [min, max]
// minmaxmaxmin
function getRandomInt(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min)  // Math.random() * (max - min + 1)max-min(max)minmaxmin
}

// 
export function shuffle(arr) {
  let _arr = arr.slice()
  for (let i = 0; i < _arr.length; i++) {
    let j = getRandomInt(0, i)
    let t = _arr[i]
    _arr[i] = _arr[j]
    _arr[j] = t
  }
  return _arr
}

// search-box http://www.alloyteam.com/2012/11/javascript-throttle/
export function debounce(func, delay) {
  let timer

  return function (...args) {     
    if (timer) {
      clearTimeout(timer)
    }
    timer = setTimeout(() => {  //       
      func.apply(this, args)
    }, delay)
  }
}