//mapGetters


import {mapGetters, mapMutations, mapActions} from 'vuex'
import {playMode} from '@/common/js/config'
import {shuffle} from '@/common/js/util' //  

export const playlistMixin = {
  computed: {
    ...mapGetters([ // mapGettersplaylist
      'playlist'
    ])
  },
  mounted() {
    this.handlePlaylist(this.playlist)
  },
  activated() { // keep-alive
    this.handlePlaylist(this.playlist)
  },
  watch: {
    playlist(newVal) {  // playlist
      this.handlePlaylist(newVal)
    }
  },
  methods: {
    handlePlaylist() { // playlist (playlistMixinhandlePlaylist)
      throw new Error('component must implement handlePlaylist method')  //
    }
  }
}

export const playerMixin = {  
  computed: {
    iconMode() {  // 
      return this.mode === playMode.sequence ? 'icon-sequence' : this.mode === playMode.loop ? 'icon-loop' : 'icon-random'
    },
    ...mapGetters([
      'sequenceList',
      'playlist',
      'currentSong',
      'mode',
      'favoriteList'
    ])
  },
  methods: {
    changeMode() {  // 
      const mode = (this.mode + 1) % 3  // (this.mode + 1)
      this.setPlayMode(mode)  // mode
      let list = null
      if (mode === playMode.random) {  // 
        list = shuffle(this.sequenceList)
      } else { // 
        list = this.sequenceList
      }
      this.resetCurrentIndex(list) 
      this.setPlaylist(list)  // playlistcurrentSong.resetCurrentIndex
    },
    resetCurrentIndex(list) { //currentSongcurrentSongplaylistcurrentIndex
      let index = list.findIndex((item) => {  // findIndexes6,
        return item.id === this.currentSong.id
      })
      this.setCurrentIndex(index)
    },
    toggleFavorite(song) {
      if (this.isFavorite(song)) {
        this.deleteFavoriteList(song)
      } else {
        this.saveFavoriteList(song)
      }
    },
    getFavoriteIcon(song) {
      if (this.isFavorite(song)) {
        return 'icon-favorite'
      }
      return 'icon-not-favorite'
    },
    isFavorite(song) {  // 
      const index = this.favoriteList.findIndex((item) => {
        return item.id === song.id
      })
      return index > -1
    },
    // 
    ...mapMutations({
      setPlayMode: 'SET_PLAY_MODE',
      setPlaylist: 'SET_PLAYLIST',
      setCurrentIndex: 'SET_CURRENT_INDEX',
      setPlayingState: 'SET_PLAYING_STATE'
    }),
    ...mapActions([
      'saveFavoriteList',
      'deleteFavoriteList'
    ])
  }
}

export const searchMixin = {  // 
  data() {
    return {
      query: '',
      refreshDelay: 120  //ok
    }
  },
  computed: {
    ...mapGetters([
      'searchHistory'  // 
    ])
  },
  methods: {
    onQueryChange(query) {   // queryquerysearch-boxnewQuery
      this.query = query
    },
    blurInput() {  // input ( search@listScrollinput )
      this.$refs.searchBox.blur()  // blur
    },
    addQuery(query) {  //  search-boxsetQuery
      this.$refs.searchBox.setQuery(query)
    },
    saveSearch() {
      this.saveSearchHistory(this.query)  // query
    },
    ...mapActions([
      'saveSearchHistory',
      'deleteSearchHistory'
    ])
  }
}
